@import url(https://fonts.googleapis.com/css2?family=Open+Sans&family=Playfair+Display&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Open Sans", sans-serif;
}

/* font-family: 'Open Sans', sans-serif;
font-family: 'Playfair Display', serif;
#FFA2B9 */

.header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  padding: 40px 50px;
}
.logo {
  font-family: "Playfair Display", serif;
  color: #d82650;
  text-decoration: none;
  font-size: 1.8rem;
  font-weight: bold;
  margin-right: 50px;
}
.search__bar {
  display: flex;
  align-items: center;
  flex: 1 1;
}
.search__input {
  height: 46px;
  padding: 15px;
  width: 100%;
  font-size: 1rem;
  outline: none !important;
  background-color: #eeeeee;
  border: none;
}
.search__input::-webkit-input-placeholder {
  font-size: 0.9rem;
}
.search__input:-ms-input-placeholder {
  font-size: 0.9rem;
}
.search__input::placeholder {
  font-size: 0.9rem;
}
.search__icon {
  background-color: #d82650;
  color: #fff;
  padding: 12px 20px;
  font-size: 1rem;
  cursor: pointer;
  border: none;
  outline: none;
  transition: 0.3s;
}
.search__icon:hover {
  background-color: #000;
}
.nav__list {
  display: flex;
}
.nav__item {
  list-style: none;
  margin-left: 50px;
}
.nav__link {
  color: gray;
  font-size: 1rem;
  text-decoration: none;
}
.nav__item:hover .nav__link {
  color: #000;
}
.nav__active {
  color: #000;
  font-weight: bold;
}

.banner {
  display: flex;
  align-items: center;
  justify-content: space-around;
  padding: 50px;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.1);
}
.banner__heading {
  color: #000;
  font-size: 3rem;
  width: 40%;
  font-family: "Playfair Display", serif;
  line-height: 1.5;
}
.banner__img {
  width: 40%;
  height: 350px;
}

.section__width {
  width: 100%;
  max-width: 1500px;
  margin-left: auto;
  margin-right: auto;
}
.news__section {
  width: 90%;
  margin: 10% auto;
}

.news__section__heading {
  font-size: 2.3rem;
  word-spacing: 5px;
  font-family: "Playfair Display", serif;
  text-align: center;
  padding: 10px;
  border-top: 2px solid;
  border-bottom: 2px solid;
}

.sidebar__weather__section {
  margin: 10% 0;
  display: flex;
  align-items: flex-start;
  justify-content: space-around;
}
.sidebar__news {
  display: flex;
  flex-direction: column;
  width: 40%;
}
.sidebar__news__heading {
  font-size: 1.5rem;
  margin-bottom: 30px;
  color: #d82650;
}

.weather__section {
  width: 35%;
}
.weather__heading {
  text-align: center;
  padding: 10px;
  font-size: 1.3rem;
  background-color: #d82650;
  color: #fff;
}
.weather__detail__section {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-top: 40px;
  padding: 0 30px 30px;
}
.weather__detail p:nth-child(1) {
  font-size: 1.2rem;
  font-weight: bold;
}
.weather__detail p:nth-child(2) {
  font-size: 1.7rem;
  font-weight: bold;
  margin: 20px 0 40px;
}
.weather__detail p:nth-child(3) {
  font-size: 1rem;
}
.weather__detail__section img {
  width: 80px;
}

.sidebar__detail {
  display: flex;
  padding: 15px 0;
  border-bottom: 1px solid #cccccc;
}
.sidebar__detail__img {
  width: auto;
  height: 50px;
  object-fit: contain;
  border: 1px solid;
}
.sidebar__detail__heading {
  font-size: 1rem;
  margin-left: 20px;
}

